
.about {

  &__row {

    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;

  }

  &__text {

    font-family: $secondaryFont;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.25;
    color: $white;

    margin-bottom: 30px;

  }

  &__info {

    width: 460px;

    @media (max-width: 939px) {

      width: 100%;
      margin-bottom: 30px;

    }

    h2 {

      margin-bottom: 30px;

    }

  }

  &__docs {

    width: calc(100% - 460px - 130px);

    @media (max-width: 1180px) {

      width: calc(100% - 460px - 30px);

    }

    @media (max-width: 939px) {

      width: 100%;
      max-width: 639px;
      margin: 0 auto;

    }

    &-row {

      display: flex;
      flex-wrap: wrap;

      margin: -15px;

      @media (max-width: 767px) {
        margin: -10px;
      }

    }

    &-col {

      width: calc(50% - 30px);
      margin: 15px;

      @media (max-width: 767px) {
        width: calc(50% - 20px);
        margin: 10px;
      }

    }

  }

  &__contacts {

    &-item {

      font-family: $font;
      font-weight: normal;
      font-size: 20px;
      line-height: 1.25;
      color: $white;

      a {

        font-family: $font;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 1.25;
        color: $red;

        @include hover;

      }

      & + * {

        margin-top: 20px;

      }

    }

  }

}