
.type-input {

  input, textarea {

    width: 100%;
    padding: 13px 16px;
    background: $gray;

    font-family: $font;
    font-size: 14px;
    line-height: 1.25;
    letter-spacing: 0.06em;
    color: $white;

    &::placeholder {

      font-family: $font;
      font-size: 14px;
      line-height: 1.25;
      letter-spacing: 0.06em;
      color: $white;

      transition: color .24s ease;

    }

    &:focus {

      &::placeholder {

        color: transparent;

      }

    }

  }

}

.btn {

  button,a,input {

    padding: 12px 22px;

  }

}

.type-button {

  button,a,input {

    background: linear-gradient(135deg, #E01D2F 0%, #C20000 100%), #E01D2F;
    border: 1px solid transparent;
    text-align: center;

    @include border-transition;

    span {

      font-family: $font;
      font-size: 14px;
      line-height: 1.25;
      letter-spacing: 0.06em;
      text-transform: uppercase;

      color: $white;

    }

    &:hover {

      border-color: $white;

    }

  }

}

.checkbox {

  label {

    cursor: pointer;

  }

  input {

    display: none;

    &:checked {

      & + .checkbox__content {

        .checkbox__mark {

          .icon {

            opacity: 1;

          }

        }

      }

    }

  }

  &__content {

    padding-left: 30px;

    font-family: $secondaryFont;
    font-size: 16px;
    line-height: 1.25;
    color: $white;
    text-align: left;

  }

  &__mark {

    width: 20px;
    height: 20px;

    position: absolute;
    left: 0;
    top: 0;

    display: flex;
    justify-content: center;
    align-items: center;
    background: $red;

    .icon {

      font-size: 12px;
      color: $white;
      opacity: 0;
      top: -2px;

    }

  }

}

.form-field {

  margin-bottom: 20px;

}

.field {

  &__caption {

    font-family: $secondaryFont;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: $white;

    margin-bottom: 20px;

  }

}