
.company {

  background: $darkGray;

  &__row {

    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;

  }

  &__info {

    width: calc(100% - 560px - 130px);

    @media (max-width: 1180px) {

      width: calc(100% - 460px - 30px);

    }

    @media (max-width: 939px) {

      width: 100%;
      margin-bottom: 30px;

    }

  }

  &__steps {

    width: 560px;

    @media (max-width: 1180px) {

      width: 460px;

    }

    @media (max-width: 939px) {

      width: 100%;
      max-width: 639px;
      margin: 0 auto;

    }

    &-row {

      display: flex;
      flex-wrap: wrap;
      margin: -15px;

      @media (max-width: 939px) {

        margin: -10px;

      }

    }

    &-col {

      width: calc(50% - 30px);
      margin: 15px;

      @media (max-width: 939px) {

        width: calc(100% / 2 - 20px);
        margin: 10px;

      }

      @media (max-width: 425px) {

        width: calc(100% / 1 - 20px);

      }

    }

  }

}