
.reviews-slider {

  &__wrap {

    padding-bottom: 10px;

  }

  .swiper-slide {

    height: auto;

  }

  &__pagination {

    margin-top: 35px;

  }

  &__button {

    position: absolute;
    bottom: 0;

    @media (max-width: 1180px) {
      display: none;
    }

    &-next {

      right: 0;

    }

    &-prev {

      right: 62px;

    }

  }

}