
.type-select {

  select {
    display: none;
  }

  .select-selected {

    cursor: pointer;

    width: 100%;
    background: $gray;
    padding: 13px 36px 13px 19px;

    font-family: $font;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.25;
    letter-spacing: 0.06em;

    color: $white;

    &:after {

      content: '';
      position: absolute;
      right: 16px;
      top: 50%;
      transform: translateY(-50%);
      background: url("../img/select-arrow.svg") center no-repeat;
      width: 18px;
      height: 18px;
      background-size: 18px;

    }

    &.select-arrow-active {

      &:after {

        transform: translateY(-50%) rotate(180deg);

      }

    }

  }

  .select-items {

    z-index: 6;

    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: $gray;
    width: 100%;

    overflow: hidden;
    max-height: 175px;
    overflow-y: auto;

    border-top: 1px solid $red;

    & > * {

      cursor: pointer;

      padding: 13px 19px;
      font-family: $font;
      font-weight: normal;
      font-size: 14px;
      line-height: 1.25;
      letter-spacing: 0.06em;

      &.same-as-selected {
        font-weight: 500;
        background: rgba(255, 255, 255, 0.05) !important;
      }

      transition: background-color .24s ease;

      &:hover {

        background: rgba(255, 255, 255, 0.1);

      }

    }

    &.select-hide {

      display: none;

    }

  }

}