
.type-form {

  &__row {

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

  }

  &__text {

    width: 270px;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: $white;

    @media (max-width: 939px) {

      width: 100%;
      margin-bottom: 20px;

    }

  }

  &__group {

    width: calc(100% - 270px - 60px);
    display: flex;
    flex-wrap: wrap;
    margin: -15px;

    @media (max-width: 939px) {

      width: 100%;
      margin: 0;
      margin-bottom: -15px;

    }

    &-col {

      width: calc(100% / 3 - 30px);
      margin: 15px;

      @media (max-width: 1180px) {

        width: calc(100% / 2 - 30px);

      }

      @media (max-width: 939px) {

        width: 100%;
        margin: 0;
        margin-bottom: 20px;

      }

    }

    .btn {

      & > * {

        @media (max-width: 939px) {

          width: 100%;
          text-align: center;

        }

      }

    }

  }

}