
.service-card {

  height: 100%;
  display: flex;
  flex-direction: column;

  &__icon {

    margin-bottom: 30px;

    @media (max-width: 939px) {

      margin-bottom: 15px;

    }

    a {

      height: 50px;

      img {

        height: 100%;
        max-width: 100%;

        @include object-image;

      }

      @include hover;

    }

  }

  &__title {

    flex-grow: 1;
    margin-bottom: 15px;

    a {

      font-family: $font;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 1.25;
      letter-spacing: 0.01em;
      color: $white;

      @include hover;

      @media (max-width: 939px) {

        font-size: 18px;

      }

    }

  }

  &__link {

  }

}