
.review {

  height: 100%;

  &__head {

    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 20px;

  }

  &__name {

    font-family: $font;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.4;
    letter-spacing: 0.01em;
    color: $white;

  }

  &__date {

    font-family: $secondaryFont;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.25;
    color: $white;

  }

  &__text {

    font-family: $secondaryFont;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: $white;

  }

}