
.contact-item {

  padding-left: 20px;

  .icon {

    color: $red;
    font-size: 16px;

    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);

  }

  a, span {

    font-family: $secondaryFont;
    color: $white;

    font-size: 14px;

    @media (max-width: 1180px) {

      font-size: 13px;

    }

  }

  a {


    @include hover;

  }

}

.social {

  display: flex;

  & > * {

    & + * {

      margin-left: 20px;

    }

  }

  a {

    width: 20px;
    height: 20px;

    img {

      width: 100%;
      height: 100%;

      @include object-image;

    }

    @include hover;

  }

}

.sub-menu {

  z-index: 5;

  position: absolute;
  width: 200px;
  padding-top: 20px;

  left: 0;
  top: 100%;

  opacity: 0;
  visibility: hidden;
  pointer-events: none;

  @include default-fade;

  @media (max-width: 939px) {

    position: relative;
    display: none;
    width: 100%;
    top: initial;

    opacity: 1;
    visibility: visible;
    pointer-events: initial;

  }

  ul {

    background: $gray;
    padding: 20px;

    @media (max-width: 939px) {

      padding: 0;
      background: transparent;

    }

  }

  li {

    a {

      color: $white;
      font-size: 16px;
      font-family: $font;

      @include hover;

    }

    & + * {

      margin-top: 5px;

    }

  }

}

.services-menu {

  position: absolute;
  padding: 50px 100px 30px;
  z-index: 10;

  width: calc(100% - 30px);

  top: 100%;
  left: 50%;
  transform: translateX(-50%);

  column-count: 3;

  page-break-inside: avoid;
  break-inside: avoid;

  opacity: 0;
  visibility: hidden;
  pointer-events: none;

  @include default-fade;

  @media (max-width: 939px) {

    position: relative;
    opacity: 1;
    visibility: visible;
    pointer-events: initial;

    display: none;

    width: 100%;
    top: initial;
    left: initial;
    transform: none;

    padding: 20px 0 0;

    column-count: initial;
    flex-wrap: wrap;
    justify-content: space-between;

  }

  &:before {

    content: '';
    position: absolute;
    top: 20px;
    left: 0;
    right: 0;
    bottom: 0;
    background: $gray;

    @media (max-width: 939px) {

      display: none;

    }

  }

  &__item {

    page-break-inside: avoid;
    break-inside: avoid;
    margin-bottom: 20px;

    @media (max-width: 939px) {

      width: calc(100% / 2 - 20px);

    }

    @media (max-width: 425px) {

      width: calc(100%);

    }

    &-title {

      font-family: $font;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 1.75;
      letter-spacing: 0.01em;
      color: #FBFBFB;

      margin-bottom: 20px;

    }

    ul {

      li {

        & + * {

          margin-top: 5px;

        }


        a {

          font-family: $secondaryFont;
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 1.25;
          color: $white;

          @include hover;

        }

      }

    }

  }

}

