  font-weight: 600;
  font-size: 46px;
  line-height: 1.25;
  color: $white;

  @media (max-width: 939px) {

    font-size: 36px;

  }

  @media (max-width: 767px) {

    font-size: 24px;

  }

  span {

    font-family: $font;
    font-weight: 600;
    font-size: 46px;
    line-height: 1.25;
    color: $white;

    @media (max-width: 939px) {

      font-size: 36px;

    }

    @media (max-width: 767px) {

      font-size: 24px;

    }

  }

}

h2 {

  font-family: $font;
  font-weight: 600;
  font-size: 36px;
  line-height: 1.25;
  color: $white;

  @media (max-width: 939px) {

    font-size: 24px;

  }

  @media (max-width: 767px) {

    font-size: 20px;

  }

  span {

    font-family: $font;
    font-weight: 600;
    font-size: 36px;
    line-height: 1.25;
    color: $white;

    @media (max-width: 939px) {

      font-size: 24px;

    }

    @media (max-width: 767px) {

      font-size: 20px;

    }

  }

}

h3 {

  font-family: $font;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.25;
  letter-spacing: 0.01em;
  color: $white;

  @media (max-width: 939px) {

    font-size: 18px;

  }

  @media (max-width: 767px) {

    font-size: 16px;

  }

  span {

    font-family: $font;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.25;
