}

.copyright {

  font-family: $secondaryFont;
  font-size: 12px;
  line-height: 1.25;
  color: $white;

}

.privacy {

  a {

    font-family: $secondaryFont;
    font-size: 12px;
    line-height: 1.25;
    color: $white;

    &:hover {

      text-decoration: underline;

    }

  }

}

.link {

  color: $red;
  font-family: $secondaryFont;

  &:hover {

    text-decoration: underline;

  }

}

.open-mobile-menu {

  width: 30px;
  height: 30px;
  background: $red;

  display: none;
  justify-content: center;
  align-items: center;

  @media (max-width: 939px) {
    display: flex;
  }

  .icon {

    font-size: 18px;
    color: $white;

  }

}

.close-mobile-menu {

  width: 30px;
  height: 30px;
  background: $red;

  display: none;
  justify-content: center;
  align-items: center;

  @media (max-width: 939px) {
    display: flex;
  }

  .icon {

    font-size: 18px;
    color: $white;

  }

}

.text {

  font-family: $secondaryFont;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.35;
  color: $white;

}

.panel {

  background: $gray;
  padding: 30px;

  @media (max-width: 939px) {

    padding: 20px;

  }

  @media (max-width: 425px) {

    padding: 20px 10px;

  }

}

.arrow-link {

  padding-right: 23px;

  font-family: $font;
  font-style: normal;
  font-weight: 500;

  font-size: 14px;
  line-height: 1.25;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: $red;

  &:after {

    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    background: url("../img/arrow-link.svg") center no-repeat;

    width: 16px;
    height: 12px;

    background-size: 16px 12px;

  }

  @include hover;

}

.type-list {

  li {

    padding-left: 24px;
    font-family: $secondaryFont;

    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: $white;

    &:before {

      content: '';
      position: absolute;
      width: 8px;
      height: 8px;
      background: $red;
      transform: rotate(45deg);
      left: 3px;
      top: 7px;

    }

    & + * {

      margin-top: 10px;

    }

  }

  &.column-count-2 {

    column-count: 2;
    page-break-inside: avoid;
    break-inside: avoid;

    li {

      page-break-inside: avoid;
      break-inside: avoid;

    }

  }

  &.column-count-3 {

    column-count: 3;
    page-break-inside: avoid;
    break-inside: avoid;

    li {

      page-break-inside: avoid;
      break-inside: avoid;

    }

    @media (max-width: 939px) {

      column-count: 2;

    }

  }

}

.tooltip {

  z-index: 5;

  &__button {

    width: 20px;
    height: 20px;
    background: $red;

    display: flex;
    justify-content: center;
    align-items: center;

    transition: opacity .24s ease;

    img {

      width: 2px;
      height: 10px;

      @include object-image;

    }

  }

  &__text {

    position: absolute;
    top: 0;
    right: 100%;
    background: $darkGray;
    width: 290px;
    padding: 15px;

    font-family: $secondaryFont;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.25;
    color: $white;

    opacity: 0;
    visibility: hidden;
    pointer-events: none;

    @include default-fade;

    @media (max-width: 1440px) {

      display: none !important;

    }

  }

  &:hover {

    button {
      opacity: .75;
    }

    div {
      opacity: 1;
      visibility: visible;
      pointer-events: initial;
    }

  }

}



