
.benefit-card {

  height: 100%;

  &__icon {

    height: 50px;
    margin-bottom: 20px;

    img {

      max-width: 100%;
      max-height: 100%;

      @include object-image;

    }

  }

  &__title {

    font-family: $font;
    font-weight: 500;
    font-size: 24px;
    line-height: 1.25;
    letter-spacing: 0.01em;
    color: $white;

    margin-bottom: 20px;

    @media (max-width: 939px) {

      font-size: 18px;

    }

    @media (max-width: 767px) {

      margin-bottom: 0px;

    }

  }

  &__text {

    font-family: $secondaryFont;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: $white;

    @media (max-width: 767px) {

      display: none;

    }

  }

}