
.case {

  &__slider {

    padding-bottom: 10px;

    .swiper-slide {

      height: auto;

    }

    &-pagination {

      margin-top: 25px;

    }

    &-button {

      position: absolute;
      bottom: 0;

      @media (max-width: 767px) {

        display: none;

      }

      &-prev {

        right: 62px;

      }

      &-next {

        right: 0px;

      }

    }

  }

  &__slide {

    display: flex;
    height: 100%;

    @media (max-width: 767px) {

      display: block;

    }

    &-image {

      width: 560px;
      min-height: 400px;
      height: auto;

      overflow: hidden;

      @media (max-width: 1180px) {

        width: 50%;

      }

      @media (max-width: 767px) {

        width: 100%;
        min-height: initial;
        height: 200px;

      }

      img {

        width: 100%;
        height: 100%;

        object-fit: cover;
        object-position: center;

      }

    }

    &-content {

      width: calc(100% - 560px);
      height: auto;
      background: $gray;
      padding: 42px 42px 42px 58px;

      @media (max-width: 1180px) {

        width: 50%;

      }

      @media (max-width: 939px) {

        padding: 30px 20px;

      }

      @media (max-width: 767px) {

        width: 100%;
        min-height: calc(100% - 200px);

      }

    }

    &-title {

      font-family: $font;
      font-weight: 600;
      font-size: 28px;
      line-height: 1.25;
      color: $white;

      margin-bottom: 30px;

    }

    &-text {

      font-family: $secondaryFont;
      font-weight: normal;
      font-size: 16px;
      line-height: 1.25;
      color: $white;

      margin-bottom: 30px;

    }

  }

}