
.type-content {

  h1,h2,h3,h4,h5,ul,ol {

    margin-bottom: 30px;

    @media (max-width: 939px) {

      margin-bottom: 20px;

    }

  }

  p {

    font-family: $secondaryFont;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.5;
    color: $white;

    margin-bottom: 20px;

  }

  & > * {

    &:last-child {

      margin-bottom: 0;

    }

  }

}