* {
  scroll-behavior: smooth;
}

.type-list {

  li {

    span {

      font-family: $secondaryFont;
      font-weight: bold;
      font-size: 18px;
      line-height: 1.25;

      color: $red;


    }

  }

}

.welcome {

  .type-list {

    margin-bottom: 45px;

    @media (max-width: 939px) {

      margin-bottom: 25px;

    }

  }

}

.footer__landing {

  background: $black;
  padding: 22px 0;

  .privacy {

    text-align: right;

    @media (max-width: 939px) {
      text-align: center;
    }

  }

  .copyright {

    @media (max-width: 939px) {
      text-align: center;
    }

  }

  &-row {

    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 939px) {

      display: block;

    }

  }

  &-col {

    &:first-child {

      display: flex;
      align-items: center;

      @media (max-width: 939px) {

        justify-content: center;
        margin-bottom: 20px;

      }

      @media (max-width: 639px) {

        display: block;

      }

      & > * {

        @media (max-width: 639px) {

          max-width: 162px;
          margin: 0 auto!important;
          margin-bottom: 10px!important;

        }

        & + * {

          margin-left: 40px;

          @media (max-width: 939px) {

            margin: 0 20px;

          }

          @media (max-width: 639px) {

            margin: 0;

          }

        }

      }

    }

  }

}

.transparent-bg {

  background: transparent !important;

}

.header {

  &__menu {

    margin-top: 0;

  }

  &__subrow {

    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-top: 20px;

    @media (max-width: 939px) {

      margin-top: 0px;

    }

  }

  &__subcol {

    display: flex;
    align-items: center;

    & > * {

      & + * {

        margin-left: 20px;

      }

    }

    &:last-child {

      @media (max-width: 939px) {

        display: none;

      }

    }

  }

  &__panel {

    &-col {

      &:last-child {

        @media (max-width: 425px) {

          margin-left: 10px;

        }

      }

    }

  }

}


#policy {

  .type-modal__dialog {

    max-width: calc(100% - 100px);

    @media (max-width: 767px) {

      max-width: 100%;

    }

  }

  .type-modal__content {
    text-align: left;
    margin-bottom: 20px;
  }

  .btn {

    text-align: center;

    & > * {

      width: initial;

      @media (max-width: 767px) {

        width: 100%;

      }

    }

  }

}

.map {

  margin-top: 50px;

  @media (max-width: 939px) {

    margin-top: 30px;

  }

  &__form {

    top: initial;

    @media (max-width: 939px) {

      padding-bottom: 0;

    }

  }

}