
.new-card {

  height: 100%;
  display: flex;
  flex-direction: column;

  &__image {

    width: 100%;
    height: 230px;
    overflow: hidden;

    img {

      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;

    }

  }

  &__panel {

    min-height: calc(100% - 230px);
    display: flex;
    flex-direction: column;

    padding: 25px 30px;
    background: $gray;

    @media (max-width: 939px) {

      padding: 20px;

    }

  }

  &__title {

    flex-grow: 1;
    margin-bottom: 20px;

    a {

      font-family: Jost;
      font-style: normal;
      font-weight: 500;
      font-size: 20px;
      line-height: 1.4;
      letter-spacing: 0.01em;
      color: $white;

      transition: color .24s ease;

      @media (max-width: 939px) {
        font-size: 18px;
      }

      &:hover {

        color: $red;

      }

    }

  }

  &__text {

    font-family: $secondaryFont;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: $white;

    margin-bottom: 20px;

  }

  &__foot {

    display: flex;
    justify-content: space-between;
    align-items: center;

  }

  &__date {

    font-family: $secondaryFont;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.25;
    color: $white;

  }

}