
.clients {

  &__row {

    display: flex;
    flex-wrap: wrap;
    margin: -27px;

  }

  &__col {

    width: calc(100% / 6 - 54px);
    margin: 27px;

    @media (max-width: 1180px) {

      width: calc(100% / 4 - 54px);

    }

    @media (max-width: 767px) {

      width: calc(100% / 3 - 54px);

    }

    @media (max-width: 425px) {

      width: calc(100% / 2 - 54px);

    }

  }

}

.client {

  display: flex;
  justify-content: center;
  align-items: center;

  height: 100%;

  img {

    max-width: 100%;
    max-height: 100%;

    @include object-image;

  }

}