
.documents-list {

  &__row {

    display: flex;
    flex-wrap: wrap;
    margin: -15px;

    @media (max-width: 939px) {

      margin: -10px;

    }

  }

  &__col {

    width: calc(100% / 4 - 30px);
    margin: 15px;
    height: auto;

    @media (max-width: 939px) {

      width: calc(100% / 2 - 20px);
      margin: 10px;

    }

  }

}