
.uniform-preview {

  width: 100%;
  height: 100%;

  background: $gray;
  padding: 20px 20px 10px;

  transition: background-color .24s ease;

  &__image {

    overflow: hidden;
    opacity: .75;

    transition: opacity .24s ease;

    &:before {

      content: '';
      padding-top: 160%;
      position: relative;
      display: block;

    }

    img {

      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;

    }

  }

  &__text {

    margin-top: 10px;

    font-family: $font;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.25;
    text-align: center;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: $white;

  }

  &:hover {

    background: $red;

    .uniform-preview__image {

      opacity: 1;

    }

  }

}