
@mixin border-transition {
  transition: border-bottom-color .24s ease, border-right-color .24s ease , border-left-color .24s ease , border-top-color .24s ease;
}

@mixin bg-border-transition {
  transition: background-color .24s ease ,border-bottom-color .24s ease, border-right-color .24s ease , border-left-color .24s ease , border-top-color .24s ease;
}

@mixin default-fade {
  transition: opacity .34s ease , visibility .34s ease;
}

@mixin cover-image {
  position: absolute;
  width: 100%;
  height: auto;
  min-height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  object-fit: cover;
}

@mixin object-image {

  object-fit: contain;
  object-position: center;

}

@mixin hover {

  transition: opacity .24s ease;

  &:hover {

    opacity: .75;

  }

}