
.tariff {

  &__text {

    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: $white;
    margin-bottom: 30px;

  }

  &__moments {

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: -30px;

    @media (max-width: 1180px) {

      margin-bottom: 20px;

    }

    &-col {

      width: calc(100% / 4 - 60px);
      margin-bottom: 30px;

      @media (max-width: 1180px) {

        width: calc(100% / 2 - 20px);
        margin-bottom: 20px;

      }

      @media (max-width: 939px) {

        width: calc(100%);

      }

    }

    &-item {

      display: flex;
      align-items: center;
      justify-content: space-between;

      &-icon {

        width: 50px;
        height: 50px;

        img {

          width: 100%;
          height: 100%;

          @include object-image;

        }

      }

      &-content {

        width: calc(100% - 50px - 18px);

      }

      &-title {

        font-family: $font;
        font-weight: 500;
        font-size: 20px;
        line-height: 1.4;
        letter-spacing: 0.01em;
        color: $white;

        margin-bottom: 3px;

      }

      &-text {

        font-family: $secondaryFont;
        font-weight: normal;
        font-size: 16px;
        line-height: 1.25;
        color: $white;

      }

    }

  }

  &__slider {

    margin-top: 45px;

    .swiper-container {

      overflow: initial;

    }

    .swiper-wrapper {

      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      margin: -15px;
      width: initial;

      @media (max-width: 939px) {

        width: 100%;
        margin: 0;
        flex-direction: row;
        flex-wrap: nowrap;

      }

      .swiper-slide {

        width: calc(100% / 2 - 30px);
        margin: 15px;
        height: auto;

        @media (max-width: 939px) {

          margin: 0;
          width: initial;

        }

      }

    }

    &-pagination {

      display: none;

      @media (max-width: 939px) {

        display: flex;
        margin-top: 20px;

      }

    }

  }

}