
.type-counter {

  width: 100%;
  display: flex;

  input {

    width: 70px;
    height: 45px;
    background: $gray;

    font-family: $font;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    letter-spacing: 0.06em;
    color: $white;

    text-align: center;
    padding: 10px 5px;

    @media (max-width: 767px) {

      width: 100px;

    }

  }

  &__minus {

    height: 45px;
    width: calc((100% - 70px) / 2);
    background: $darkGray;

    cursor: pointer;

    @media (max-width: 767px) {

      width: calc((100% - 100px) / 2);

    }

    &:before {

      content: '';

      width: 18px;
      height: 1px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      background: $red;

    }

  }

  &__plus {

    height: 45px;
    width: calc((100% - 70px) / 2);
    background: $darkGray;

    cursor: pointer;

    @media (max-width: 767px) {

      width: calc((100% - 100px) / 2);

    }


    &:before {

      content: '';

      width: 18px;
      height: 1px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      background: $red;

    }

    &:after {

      content: '';
      position: absolute;
      height: 18px;
      width: 1px;
      background: $red;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

    }

  }

}