
.steps {

  display: flex;
  flex-wrap: wrap;
  margin: -15px;

  @media (max-width: 767px) {

    margin: -10px;

  }

  &__col {

    width: calc(100% / 4 - 30px);
    margin: 15px;

    @media (max-width: 939px) {

      width: calc(100% / 2 - 30px);
      margin: 15px;

    }

    @media (max-width: 767px) {

      width: calc(100% / 2 - 20px);
      margin: 10px;

    }

    @media (max-width: 425px) {

      width: calc(100% / 1 - 20px)

    }

  }

  &__item {

    height: 100%;

    &-number {

      font-family: $font;
      font-weight: 600;
      font-size: 46px;
      line-height: 1.25;
      color: $red;

      margin-bottom: 10px;

      @media (max-width: 767px) {

        font-size: 32px;

      }

    }

    &-title {

      font-family: $font;
      font-weight: 500;
      font-size: 20px;
      line-height: 1.25;
      letter-spacing: 0.01em;
      color: $white;

    }

  }

}
