
.tariff-card {

  height: 100%;
  display: flex;
  flex-direction: column;

  &__head {

    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    &-col {

      display: flex;
      align-items: center;

    }


  }

  &__title {

    font-family: $font;
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 1.25;
    letter-spacing: 0.01em;
    color: $white;

  }

  &__price {

    font-family: $font;
    font-weight: normal;
    font-size: 24px;
    line-height: 1.25;
    color: $white;

    margin-bottom: 20px;

    span {

      font-family: $font;
      font-style: normal;
      font-weight: 600;
      font-size: 36px;
      line-height: 1.25;
      color: $red;

    }

  }

  &__info {

    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 20px;

    @media (max-width: 1180px) {

      flex-wrap: wrap;

    }

  }

  &__col {

    width: calc(50% - 15px);

    @media (max-width: 1180px) {

      width: 100%;

    }

    & + * {

      @media (max-width: 1180px) {

        margin-top: 20px;

      }

    }

  }

  &__list {

    li {

      display: flex;
      justify-content: space-between;
      align-items: center;

      span {

        font-family: $secondaryFont;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 1.25;
        color: $white;

      }

      & + * {

        margin-top: 10px;

      }

    }

  }

  .checkbox {

    & + * {

      margin-top: 20px;

    }

  }

  &__button {

    margin-top: 30px;

    & > * {

      min-width: 50%;
      text-align: center;

    }

  }

  &__body {

    flex-grow: 1;

  }

}

.weapon {

  margin-left: 24px;

  img {

    width: 38px;
    height: 38px;

    @include object-image;

    &:first-child {
      display: block;
    }

    &:last-child {
      display: none;
    }

  }

  &.active {

    img {

      &:first-child {
        display: none;
      }

      &:last-child {
        display: block;
      }

    }

  }

}