
.welcome {

  padding: 100px 0 110px;
  margin-bottom: 60px;
  background: $darkGray;

  @media (max-width: 939px) {

    padding: 60px 0;
    margin-bottom: 30px;

  }

  @media (max-width: 767px) {

    padding: 40px 0;

  }

  &__bg {

    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;

    img {

      width: 100%;
      height: 100%;

      object-position: right;
      object-fit: cover;

      &:first-child {
        display: block;
        @media (max-width: 939px) {
          display: none;
        }
      }

      &:last-child {
        display: none;
        @media (max-width: 939px) {
          display: block;
        }
      }

    }

    &:after {

      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: $darkGray;
      opacity: .7;

      display: none;

      @media (max-width: 1180px) {

        display: block;

      }

    }

  }

  .container {

    z-index: 2;

  }

  &__content {

    max-width: 660px;

    @media (max-width: 767px) {

      max-width: 100%;

    }

  }

  h1 {

    margin-bottom: 30px;

  }

  .text {

    margin-bottom: 45px;

  }

  &__form {

    display: flex;
    flex-wrap: wrap;

    margin: -15px;

    &-field {

      margin: 15px;
      width: calc(100% / 3 - 30px);

      @media (max-width: 767px) {

        width: calc(100% / 1 - 30px);

      }

      button, a {

        width: 100%;

      }

    }

  }

}