
.doc-preview {

  width: 100%;

  &__panel {

    padding: 15px 30px;
    background: $gray;

    transition: background-color .24s ease;

  }

  &__image {

    &:before {

      content: '';
      position: relative;
      display: block;
      padding-top: 133%;

    }

    img {

      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;

      width: 100%;
      height: 100%;

      object-fit: cover;
      object-position: center;

    }

    opacity: .75;

  }

  &:hover {

    .doc-preview__panel {
      background: $red;
    }

    .doc-preview__image {

      opacity: 1;

    }

  }

  &__title {

    font-family: $secondaryFont;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.25;
    color: $white;
    text-align: center;

    margin-top: 15px;

  }

}