
.file-card {

  transition: background-color;
  height: 100%;
  display: flex;
  flex-direction: column;

  &__icon {

    margin-bottom: 30px;

    @media (max-width: 939px) {

      margin-bottom: 15px;

    }

    .icon {

      color: $red;
      font-size: 50px;

      @media (max-width: 939px) {

        font-size: 25px;

      }

    }

  }

  &__title {

    font-family: Jost;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.4;
    letter-spacing: 0.01em;
    color: $white;

    flex-grow: 1;

    margin-bottom: 15px;

    @media (max-width: 939px) {

      font-size: 18px;

    }

    @media (max-width: 425px) {

      font-size: 14px;

    }

  }

  &:hover {

    background: $red;

    .icon {

      color: $white;

    }

    .arrow-link {

      color: #fff;

      &:after {

        filter: brightness(0) invert(1);

      }

    }

  }

}