
.card-slider {

  &__wrap {

    margin-top: 40px;
    padding-bottom: 10px;

  }

  &__pagination {

    margin-top: 35px;

  }

  &__button {

    position: absolute;
    bottom: 0;

    @media (max-width: 1180px) {

      display: none;

    }

    &-next {
      right: 0;
    }

    &-prev {
      right: 62px;
    }

  }

}