
.uniform {

  &__slider {

    padding-bottom: 10px;
    margin-top: 40px;

    .swiper-slide {

      height: auto;

    }

    &-pagination {

      margin-top: 35px;

    }

    &-button {

      position: absolute;
      bottom: 0;

      @media (max-width: 1180px) {
        display: none;
      }

      &-next {

        right: 0;

      }

      &-prev {

        right: 62px;

      }

    }

  }

}