/*fonts*/

// Jost

@font-face {
  font-family: 'Jost';
  src: local('Jost Regular'), local('Jost-Regular'),
  url('../fonts/Jost-Regular.woff2') format('woff2'),
  url('../fonts/Jost-Regular.woff') format('woff'),
  url('../fonts/Jost-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Jost';
  src: local('Jost Medium'), local('Jost-Medium'),
  url('../fonts/Jost-Medium.woff2') format('woff2'),
  url('../fonts/Jost-Medium.woff') format('woff'),
  url('../fonts/Jost-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Jost';
  src: local('Jost SemiBold'), local('Jost-SemiBold'),
  url('../fonts/Jost-SemiBold.woff2') format('woff2'),
  url('../fonts/Jost-SemiBold.woff') format('woff'),
  url('../fonts/Jost-SemiBold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
}


// IBM Plex Sans

@font-face {
  font-family: 'IBM Plex Sans';
  src: url('../fonts/IBMPlexSans.eot');
  src: url('../fonts/IBMPlexSans.eot?#iefix') format('embedded-opentype'),
  url('../fonts/IBMPlexSans.woff') format('woff'),
  url('../fonts/IBMPlexSans.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'IBM Plex Sans';
  src: url('../fonts/IBMPlexSans-Medium.eot');
  src: url('../fonts/IBMPlexSans-Medium.eot?#iefix') format('embedded-opentype'),
  url('../fonts/IBMPlexSans-Medium.woff') format('woff'),
  url('../fonts/IBMPlexSans-Medium.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

/*body*/

html {

  height: 100%;

}

body {

  font-style: normal;
  margin: 0;
  padding: 0px;
  background-size: cover;
  font-family: $font, $secondaryFont, sans-serif;
  font-display: swap;
  background: $black;
  line-height: 1.25;
  height: 100%;
  color: $white;

  &.hidden {

    max-height: 100%;
    overflow: hidden;

  }

}

a,
button{
  color: $white;
}

p, li{
  line-height: initial;
}

.text-center {
  text-align: center !important;
}

input {

  font-family: $secondaryFont;

}

textarea {

  font-family: $secondaryFont;

}

.wrapper {

  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  transition: none;
  overflow: hidden;

  padding-top: 116px;

  @media (max-width: 939px) {

    padding-top: 92px;

  }

}

.container {

  width: 100%;
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 15px;

}

main {

  z-index: 2;

}

.footer {

  display: block;
  margin: auto 0 0 0;

}

ol {
  counter-reset: li;
  li {
    &:before {
      content: counter(li);
      counter-increment: li;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.tabs__item {

  display: none;

  &.active {

    display: block;

  }

}

.section {

  padding: 60px 0;

  @media (max-width: 939px) {

    padding: 30px 0;

  }

  h2 {

    margin-bottom: 50px;

    @media (max-width: 939px) {

      margin-bottom: 30px;

    }

    @media (max-width: 425px) {

      margin-bottom: 20px;

    }

  }

}