

.service-cards {

  &__row {

    display: flex;
    flex-wrap: wrap;
    margin: -15px;

    @media (max-width: 767px) {

      margin: -10px;

    }

  }

  &__col {

    height: auto;
    width: calc(100% / 4 - 30px);
    margin: 15px;

    @media (max-width: 939px) {

      width: calc(100% / 3 - 30px);

    }

    @media (max-width: 767px) {

      width: calc(100% / 2 - 20px);
      margin: 10px;

    }

    @media (max-width: 425px) {

      width: calc(100% / 1 - 20px);
      margin: 10px;

    }

  }

}