
.type-modal {

  z-index: 110;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;

  opacity: 0;
  visibility: hidden;
  pointer-events: none;

  transition: opacity .4s ease , visibility .4s ease;

  &__layout {

    max-height: 100%;
    overflow: auto;
    height: 100%;
    text-align: center;
    padding: 30px 20px;
    background: rgba(11, 11, 11, 0.7);

    &:before {

      content: '';
      display: inline-block;
      height: 100%;
      vertical-align: middle;
      margin-left: -1ch;

    }

  }

  &__dialog {

    width: 100%;
    max-width: 470px;
    display: inline-block;
    margin: 0;
    vertical-align: middle;

  }

  &__wrap {

    background: $darkGray;
    border: 5px solid $red;
    padding: 60px 75px;

    @media (max-width: 767px) {

      padding: 30px;

    }

  }

  &__close {

    position: absolute;
    top: 25px;
    right: 25px;
    z-index: 2;
    cursor: pointer;

    background: $red;
    width: 20px;
    height: 20px;

    display: flex;
    justify-content: center;
    align-items: center;

    @include hover;

    @media (max-width: 767px) {

      top: 15px;
      right: 15px;

    }

    .icon {

      font-size: 10px;
      color: $white;

    }

  }

  &__title {

    font-family: $font;
    font-style: normal;
    font-weight: 600;
    font-size: 30px;
    line-height: 1.25;
    color: $white;

    margin-bottom: 20px;

    @media (max-width: 767px) {

      font-size: 24px;

    }

  }

  &__text {

    font-family: $secondaryFont;
    font-size: 16px;
    line-height: 1.25;
    color: $white;
    text-align: left;

    margin-bottom: 20px;

  }

  .btn {

    & > * {
      width: 100%;
    }

  }

  &.active {

    display: block !important;
    opacity: 1;
    visibility: visible;
    pointer-events: initial;

    .type-modal__dialog {

      animation: modal-open .4s ease forwards;

    }

  }

}

.tooltip-modal {

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 120;
  max-height: 100%;

  overflow: hidden;

  &__overlay {

    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(11, 11, 11, 0.7);
    z-index: 2;

  }

  &__dialog {

    z-index: 5;
    width: 100%;
    padding: 30px 20px;

    background: $white;
    color: $black;
    font-size: 18px;
    font-family: $secondaryFont;

    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;

    max-height: 100%;

    overflow: hidden;
    overflow-y: auto;

  }

  &__close {

    position: absolute;
    right: 10px;
    top: 10px;
    color: $black;
    font-size: 14px;
    font-family: $secondaryFont;

    &:hover {

      text-decoration: underline;

    }

  }

}

@keyframes modal-open {
  from {
    transform: translateY(-250px);
    opacity: 0;
    visibility: hidden;
  }
  to {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }
}

