
.footer {

  &__main {

    padding: 45px 0 16px;

    @media (max-width: 767px) {

      padding: 30px 0 16px;

    }

    &-row {

      display: flex;
      align-items: flex-start;
      justify-content: space-between;

      @media (max-width: 939px) {

        flex-wrap: wrap;

      }

    }

    &-col {

      &:first-child {

        width: 200px;

        @media (max-width: 939px) {

          width: 100%;

        }

      }

      &:last-child {

        width: calc(100% - 200px - 100px);

        display: flex;
        justify-content: space-between;
        align-items: flex-start;

        @media (max-width: 939px) {

          width: 100%;
          margin-top: 30px;

        }

        @media (max-width: 767px) {

          display: block;
          margin-top: 20px;

        }

      }

      .logo {

        margin-bottom: 35px;

        @media (max-width: 767px) {

          margin-bottom: 20px;
          text-align: center;

        }

      }

      .contact-item {

        & + * {

          margin-top: 15px;

        }

        @media (max-width: 767px) {

          max-width: 220px;
          margin: 0 auto;
          margin-bottom: 15px;

          a {

            font-size: 18px;

          }

          span {

            font-size: 18px;

          }

          .icon {

            font-size: 16px;

          }

        }

      }

    }

  }

  &__services {

    width: calc(100% - 264px - 30px);

    column-count: 2;

    page-break-inside: avoid;
    break-inside: avoid;

    margin-bottom: -10px;

    @media (max-width: 767px) {

      width: 100%;

    }

    @media (max-width: 425px) {

      column-count: 1;

    }

    &-item {

      page-break-inside: avoid;
      break-inside: avoid;

      margin-bottom: 10px;

      &-title {

        font-family: $font;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 1.75;
        letter-spacing: 0.01em;
        color: $white;

        margin-bottom: 5px;

        @media (max-width: 939px) {

          font-size: 20px;

        }

      }

      ul {

        li {

          a {

            font-family: $secondaryFont;
            font-size: 14px;
            line-height: 1.25;
            color: $white;
            font-weight: 400;

            @media (max-width: 939px) {

              font-size: 16px;

            }

            &:hover {

              text-decoration: underline;

            }

          }

          & + * {

            margin-top: 4px;

          }

        }

      }

    }

  }

  &__info {

    width: 264px;

    @media (max-width: 767px) {

      width: 100%;
      margin-top: 40px;

    }

    &-text {

      font-family: $secondaryFont;
      font-size: 14px;
      line-height: 1.25;
      color: $white;

      margin-bottom: 40px;

    }

    &-point {

      width: 150px;
      height: 50px;
      overflow: hidden;

      & > * {

        width: 100%;
        height: 100%;

      }

    }

  }

  &__panel {

    padding: 16px 0;

    .container {

      display: flex;
      justify-content: flex-end;

      @media (max-width: 939px) {

        display: block;
        text-align: center;

      }

    }

    .copyright {

      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      @media (max-width: 939px) {

        position: relative;
        top: initial;
        left: initial;
        transform: none;
        margin-bottom: 10px;

      }

    }

  }

}