
.map {

  padding: 0;
  margin-top: 160px;

  height: 460px;

  @media (max-width: 939px) {

    height: initial;
    margin-top: 30px;

  }

  &__form {

    background: $darkGray;
    padding: 35px 70px 45px;
    z-index: 2;

    top: -100px;

    @media (max-width: 939px) {

      top: 0;
      padding: 0 0 30px;
      background: transparent;

    }

    h2 {

      margin-bottom: 30px;

    }

    &-row {

      display: flex;
      flex-wrap: wrap;
      margin: -15px;

    }

    &-col {

      width: calc(100% / 4 - 30px);
      margin: 15px;

      @media (max-width: 1180px) {

        width: calc(100% / 2 - 30px);

      }

      @media (max-width: 767px) {

        width: calc(100% / 1 - 30px);

      }

      button,a {
        width: 100%;
      }

    }

  }

  iframe {

    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;

    filter: grayscale(1);

    @media (max-width: 939px) {

      position: relative;
      height: 360px;

    }

    @media (max-width: 767px) {

      height: 280px;

    }

  }

}