
.types-list {

  background: $darkGray;
  padding: 90px 0;

  @media (max-width: 1180px) {

    padding: 60px 0;

  }

  @media (max-width: 939px) {

    padding: 30px 0;

  }

  ul {

    column-count: 3;
    page-break-inside: avoid;
    break-inside: avoid;

    @media (max-width: 939px) {

      column-count: 2;

    }

    @media (max-width: 425px) {

      column-count: 1;

    }

    li {

      page-break-inside: avoid;
      break-inside: avoid;

      @media (max-width: 939px) {

        &.hide {

          display: none;

        }

      }

      a {

        font-weight: normal;
        font-size: 16px;
        line-height: 1.5;
        color: $white;

        font-family: $secondaryFont;

        &:hover {

          text-decoration: underline;

        }

      }

    }

  }

  &__button {

    margin-top: 25px;
    display: none;
    text-align: center;

    @media (max-width: 939px) {

      display: block;

      &.hide {

        display: none;

      }

    }

  }

}