
.dignity {

  &__slider {

    padding-bottom: 10px;

    &-pagination {

      margin-top: 35px;

    }

    &-button {

      position: absolute;
      bottom: 0;

      @media (max-width: 1180px) {
        display: none;
      }

      &-next {
        right: 0;
      }

      &-prev {
        right: 62px;
      }

    }

  }

  &__item {

    padding: 10px 18px;
    width: 100%;
    background: $gray;

    transition: background-color .24s ease;

    span {

      width: 100%;

      &:before {

        content: '';
        display: block;
        padding-top: 144%;

      }

      img {

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;

        @include object-image;

        opacity: .75;
        transition: opacity .24s ease;

      }

    }

    &:hover {

      background: $red;

      img {

        opacity: 1;

      }

    }

  }

}