
.physical-calc {

  &__panel {

    background: #101010;
    display: flex;
    flex-wrap: wrap;

    @media (max-width: 767px) {

      background: transparent;

    }

  }

  &__options {

    width: calc(100% - 400px);
    padding: 40px 80px 40px 68px;

    @media (max-width: 1180px) {

      width: 100%;

    }

    @media (max-width: 767px) {

      padding: 0;

    }

    &-row {

      display: flex;
      flex-wrap: wrap;

      margin: -15px;

      @media (max-width: 767px) {

        margin: -10px;

      }

    }

    &-col {

      width: calc(100% / 2 - 30px);
      margin: 15px;

      @media (max-width: 767px) {

        width: calc(100% - 20px);
        margin: 10px;

      }

    }

  }

  &__result {

    width: 400px;
    background: $darkGray;
    padding: 30px;

    @media (max-width: 1180px) {

      width: 100%;

    }

    @media (max-width: 767px) {

      padding: 0;
      background: transparent;
      margin-top: 20px;

    }

    &-title {

      font-family: $font;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 1.25;
      letter-spacing: 0.01em;
      color: $white;

      margin-bottom: 15px;

    }

    &-price {

      font-family: $font;
      font-weight: normal;
      font-size: 24px;
      line-height: 1.25;
      color: $white;

      margin-bottom: 15px;

      span {

        font-family: $font;
        font-weight: 500;
        font-size: 36px;
        line-height: 1.25;
        color: $red;

      }

    }

  }

  &__group {

    display: flex;
    flex-wrap: wrap;
    margin: -17px;

    margin-top: 17px;

    & > * {

      margin: 17px;

    }

  }

  &__form {

    .btn {

      & > * {

        min-width: 50%;
        text-align: center;

        @media (max-width: 767px) {

          width: 100%;

        }

      }

    }

  }

}