
.form-block {

  &__wrap {

    background: $gray;
    max-width: 950px;
    margin: 0 auto;
    padding: 50px;

    @media (max-width: 767px) {

      background: transparent;
      padding: 0;

    }

    h2 {

      margin-bottom: 30px;

    }

  }

  &__phone {

    font-family: $secondaryFont;
    font-style: normal;
    font-size: 16px;
    line-height: 1.25;
    color: $white;

    margin-bottom: 30px;

    a {

      font-family: $font;
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 1.25;
      color: $red;

      @include hover;

    }

  }

  &__list {

    margin-bottom: 30px;

  }

  &__text {

    font-family: $secondaryFont;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: $white;
    margin-bottom: 30px;

  }

  &__form {

    .type-form__group-col {

      width: calc(100% / 2 - 30px);

      @media (max-width: 939px) {

        width: 100%;

      }

      input {

        background: $darkGray;

        @media (max-width: 767px) {

          background: $gray;

        }

      }

    }

  }

}