
.header {

  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  width: 100%;

  padding: 18px 0 25px;
  z-index: 30;

  background: rgba(11, 11, 11, 0.9);
  height: 116px;

  @media (max-width: 939px) {

    height: 92px;
    padding: 10px 0;
    background: $darkGray;

  }

  &__row {

    display: flex;
    justify-content: space-between;
    align-items: center;

    position: static;

  }

  &__col {

    position: static;

    &:first-child {

      width: 52px;

    }

    &:last-child {

      width: calc(100% - 52px - 90px);

      @media (max-width: 1180px) {

        width: calc(100% - 52px - 20px);

      }

      @media (max-width: 939px) {

        width: initial;

      }

    }

  }

  &__panel {

    display: flex;
    justify-content: space-between;
    align-items: center;

    &-col {

      display: flex;
      align-items: center;

      &:first-child {

        & > * {

          & + * {

            margin-left: 30px;

            @media (max-width: 1180px) {

              margin-left: 15px;

            }

          }

        }

      }

      &:last-child {

        @media (max-width: 939px) {

          margin-left: 20px;

          & > * {

            & + * {

              margin-left: 20px;

            }

          }

        }

        @media (max-width: 425px) {

          margin-left: 0;

        }

      }

    }

  }

  &__menu {

    position: static;
    margin-top: 30px;

    @media (max-width: 939px) {

      margin-top: 0;

      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;

      overflow: hidden;
      overflow-y: auto;

      z-index: 90;

      background: $gray;

      opacity: 0;
      visibility: hidden;
      pointer-events: none;

      @include default-fade;

      padding: 40px 75px;

      &.open {

        opacity: 1;
        visibility: visible;
        pointer-events: initial;

      }

      .close-mobile-menu {

        position: absolute;
        top: 31px;
        right: 15px;
        z-index: 3;

      }

    }

    @media (max-width: 767px) {

      padding: 40px;

    }

    & > ul {

      position: static;
      display: flex;

      @media (max-width: 939px) {

        position: relative;
        display: block;

      }

      & > li {

        & + * {

          margin-left: 25px;

          @media (max-width: 1180px) {

            margin-left: 15px;

          }

          @media (max-width: 939px) {

            margin-left: 0;
            margin-top: 20px;

          }

        }

        & > a {

          font-family: $font;

          font-weight: normal;
          font-size: 14px;
          line-height: 1.25;
          letter-spacing: 0.06em;
          text-transform: uppercase;
          color: $white;

          @media (max-width: 1180px) {

            font-size: 12px;

          }

          @media (max-width: 939px) {

            font-size: 20px;

          }

          &.open-modal {

            color: $red;

          }

          @include hover;

        }

        &.menu-item-has-children, &.services-item {

          & > a {

            padding-right: 15px;

            &:after {

              content: '';
              position: absolute;
              right: 0;
              top: 50%;
              transform: translateY(-50%);
              background: url("../img/arrow.svg") center no-repeat;
              width: 9px;
              height: 9px;
              background-size: 9px;
              background-position: center;

            }

          }

          &:hover {

            & > a {

              @include hover;

              &:after {

                transform: translateY(-50%) rotate(180deg);

                @media (max-width: 939px) {

                  transform: translateY(-50%);

                }

              }

            }

            .sub-menu, .services-menu {

              opacity: 1;
              visibility: visible;
              pointer-events: initial;

            }

          }

          &.open {

            @media (max-width: 939px) {

              & > a {

                &:after {

                  transform: translateY(-50%) rotate(180deg) !important;

                }

              }

              .sub-menu {

                display: block !important;

              }

              .services-menu {

                display: flex !important;

              }

            }

          }

        }

        &.services-item {

          position: static;

        }

      }

    }

  }

  .contact-item {

    &:not(.phone-number) {

      @media (max-width: 939px) {

        display: none;

      }

    }

    .icon {

      @media (max-width: 939px) {

        font-size: 20px;

      }

      @media (max-width: 425px) {

        font-size: 16px;

      }

    }

    a {

      @media (max-width: 939px) {

        font-size: 18px;

      }

      @media (max-width: 425px) {

        font-size: 16px;

      }

    }

  }

  .social {

    @media (max-width: 425px) {

      display: none;

    }

  }

}